import '../style/style.css'
// react-bootstrap
import {Card, Button, Container} from 'react-bootstrap';
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/css";
import "swiper/css/pagination";

// import required modules
import { Pagination } from "swiper";

//swiper
import project1 from '../img/brgy276.png';
import project2 from '../img/blogs.png';
import project3 from '../img/weekend.png';
import project4 from '../img/lmsjava.png';
import project5 from '../img/homepageA.png'


//animation on scroll
import AOS from 'aos';
import 'aos/dist/aos.css'; 
AOS.init();

const Projects = () => {
    return(
        <>
        <Container  fluid className='projects-div' id="Projects" animationIn="fadeUp" animationOut="bounceInUp">
        
            <Container fluid className="projects-header"><h1>My Projects so far..</h1></Container>
            <Swiper data-aos="fade-right"
                    slidesPerView={1}
                    spaceBetween={10}
                    pagination={{
                    clickable: true,
                    }}
                    breakpoints={{
                    "@0.00": {
                        slidesPerView: 1,
                        spaceBetween: 10,
                    },
                    "@0.75": {
                        slidesPerView: 2,
                        spaceBetween: 20,
                    },
                    "@1.00": {
                        slidesPerView: 3,
                        spaceBetween: 40,
                    },
                    "@1.50": {
                        slidesPerView: 4,
                        spaceBetween: 50,
                    },
                    }}
                    modules={[Pagination]}
                    className="mySwiper"
                >
                <SwiperSlide>
                    <Card>
                        <img className="swiper" src={project1}/><br/>
                        <Card.Body  className='card-body'>
                        
                            <Card.Title> <h5>E-Barangay Governance System <br/>for Brgy. 276, Binondo, Manila.</h5></Card.Title>
                            
                            <Card.Text className= "card-details">
                            A Resource Management system that caters the needs of the residents such as document requesting, e-sumbong and manages Barangay 276's resources.  <br/>  <br/> 

                            <p className='languages'>Language/s Used: HTML, Bootstrap, CSS</p>
                            </Card.Text>
                            <a href="https://barangay276.com.ph/"  target="_blank">
                               <Button className='button-projects' variant="dark" >View More</Button>
                            
                            </a>
                        </Card.Body>
                    </Card>
                </SwiperSlide>

                <SwiperSlide className='swiper-card'> 
                    <Card className='card'>
                        <img className="swiper" src={project2}/><br/>
                        <Card.Body className="card-body">
                            
                            <Card.Title> <h5><i>Thoughts</i> Blog Website</h5></Card.Title>
                            <br/><br/>
                            <Card.Text className= "card-details">
                            A blog website that showcases different personal blogs with a delete, delete all and search functionalities.<br/> <br/><br/>

                            <p className='languages'>Language/s Used: NodeJS, HTML, Bootstrap, CSS</p>
                            </Card.Text>
                            <a href="https://github.com/LJDeiparine/nodejs-blogs"  target="_blank">
                               <Button className='button-projects' variant="dark" >View More</Button>

                            </a>
                        </Card.Body>
                    </Card>
                </SwiperSlide>

                <SwiperSlide>
                    <Card  className='card'>
                        <img className="swiper" src={project3}/><br/>
                        <Card.Body  className="card-body">
                            
                            <Card.Title> <h5>My Weekend Website</h5></Card.Title>
                            <br/><br/>
                            <Card.Text className= "card-details">
                            A website that showcases what I did during the weekend by using React JS and publishing it to git.<br/> <br/><br/> <br/>

                            <p className='languages'>Language/s Used: React JS, React-Bootstrap, CSS,  Git</p>
                            </Card.Text>
                            <a href="https://ljdeiparine.github.io/Personal-Public/"  target="_blank">
                               <Button className='button-projects' variant="dark" >View More</Button>

                            </a>
                        </Card.Body>
                    </Card>
                </SwiperSlide>

                <SwiperSlide>
                    <Card>
                        <img className="swiper" src={project4}/><br/>
                        <Card.Body>
                            
                            <Card.Title> <h5>LMS Java Project</h5></Card.Title>
                            <br/>
                            <Card.Text className= "card-details">
                            A simple Java Program that showcases the login of students and faculty so that they will able to see their records.<br/> <br/><br/>

                            <p className='languages'>Language/s Used: Java</p>
                            </Card.Text>
                            <a href="https://github.com/LJDeiparine/JAVA---LMS-Project"  target="_blank">
                               <Button className='button-projects' variant="dark" >View More</Button>

                            </a>
                        </Card.Body>
                    </Card>
                </SwiperSlide>

                <SwiperSlide>
                    <Card>
                        <img className="swiper" src={project5}/><br/>
                        <Card.Body>
                            
                            <Card.Title> <h5> <i>Home</i> Website</h5></Card.Title>
                            <br/>
                            <Card.Text className= "card-details">
                           An HTML Design project that enables us to test our skills and copy the figma template that is assigned to us.<br/> <br/><br/>

                            <p className='languages'>Language/s Used: HTML, Bootstrap, CSS</p>
                            </Card.Text>
                            <a href="https://github.com/LJDeiparine/Homepage-A---Figma"  target="_blank">
                               <Button className='button-projects' variant="dark" >View More</Button>

                            </a>
                        </Card.Body>
                    </Card>
                </SwiperSlide>
            </Swiper>
        </Container>   
        </>
       
     
    )
}

export default Projects;