import React, { useEffect } from "react";
import Granim from "granim";
import { saveAs } from "file-saver";
import { BsFacebook, BsInstagram, BsLinkedin } from "react-icons/bs";
// react-bootstrap
import {Nav, Container, Navbar, Row, Col, Button} from 'react-bootstrap';

//react-animation
import {Animated} from "react-animated-css";

//css
import '../style/granim.css';
import '../style/style.css';


const Homepage =  () => {
  const saveFile = () => {
    saveAs(
      "https://drive.google.com/file/d/1ernS1qjTTpDOtojZewdSSrMLuvro6RDu/view?usp=sharing",
      "example.pdf"
    );
  };
  useEffect(() => {
    new Granim({
      element: '#canvas-image-blending',
      direction: 'top-bottom',
      isPausedWhenNotInView: true,
      image : {
          source: 'https://cutewallpaper.org/21/hd-gradient/Color-Gradient-Wallpaper-76+-images-.jpg',
          blendingMode: 'multiply',
          stretchMode: ['stretch', 'stretch'],
      },
      states : {
        
          "default-state": {
              gradients: [
                  ['#29323c', '#485563'],
                  ['#FF6B6B', '#556270'],
                  ['#80d3fe', '#7ea0c4'],
                  ['#f0ab51', '#eceba3']
              ],
              transitionSpeed: 2500
              
          }
      }
  });

  }, []);
 

return (
  <>


    <div className="content">

      <Navbar className="navbar p-2 container  navbar-fixed-top" expand="lg" id="Home">
        <Container className="navbar-container p-4">
          <Navbar.Brand id="brand" href='/'>Ljaneee.</Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav"/>
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="ms-auto">
              <Nav.Link id="link" href="https://ljdeiparine.github.io/LJLD-Portfolio-Website">Home</Nav.Link>
              <Nav.Link id="link"  href="#AboutMe">About Me</Nav.Link>
              <Nav.Link id="link" href="#Hobbies">Hobbies</Nav.Link>
              <Nav.Link id="link"  href="#Projects">Projects</Nav.Link>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>

      <canvas id="canvas-image-blending"></canvas>

      <Container fluid className="overlay">
        <Row>
          <Col className="overlay-one">
     
            <Animated animationIn="bounceInUp" animationOut="bounceInUp" isVisible={true}>
              <div className = "vertical"></div>
              <div className="social-buttons">
                <ul>
                  <li><a href="https://www.facebook.com/ljane.deiparine.16" target="_blank" title="Facebook"><BsFacebook /></a></li>
                  <li><a href="https://www.instagram.com/ljaneeed/" target="_blank" title="Instagram"><BsInstagram/></a></li>
                  <li><a href="https://www.linkedin.com/in/lhorena-jane-deiparine-579907229" target="_blank" title="Linkedin"><BsLinkedin/></a></li>
                </ul>
                
                
                
              </div>
              <div className= "text">
                <p className="homepage" >Ljane Deiparine</p>
                <Button  className='button-resume'onClick={saveFile} title="View my Resume"><p className="details">Future Web Developer</p></Button>
              </div>
            </Animated>

             
          </Col>

          <Col>
            <Animated animationIn="bounceInDown" animationOut="bounceInUp" isVisible={true}>
                <div className = "circle-container">
                  <div className="floating" >
                   
                  </div>
                </div>
            </Animated>
          </Col>
        </Row>
     </Container>

    </div>

 
    </>


);
}

export default Homepage