import '../style/style.css';

import ljane from '../img/ljane-selfie-3.png';

// react-bootstrap
import {Nav, Container, Navbar, Row, Col} from 'react-bootstrap';

//animation on scroll
import AOS from 'aos';
import 'aos/dist/aos.css'; 
AOS.init();

const AboutMe = () => {
    
    return(
        
        <>
            <Container fluid className="aboutme-div" id="AboutMe">
                <Row className="aboutme-row">
                    <Col lg={6} xs={12} md={12} > 
                        <div data-aos="fade-right" className='aboutme-one'>
                            <img className="pt-4 aboutme-picture"src={ljane}/>
                            
                        </div>
                    </Col>
                    <Col lg={6} xs={12} md={12} > 
                    
                        <div data-aos="fade-left" className='aboutme-two'>
                            
                                <h2>What am I <br/>all about</h2>

                                <p className='details-about-me'>
                                <br/>
                                
                                <br/>
                                    I am Lhorena Jane L. Deiparine, an aspiring Front End Web Developer. <br/><br/>
                                    I am a simple girl with big dreams and someday I want those dreams to be accomplished along side with my loved ones. <br/><br/>
                                    I love to do arts, exploring nature and hiking.                 <br/>
                             
                            
                               
                                <br/>
                                
                                </p>
                        
                        </div>
                    </Col>
                </Row>
            </Container>
        </>
       
     
    )
}

export default AboutMe;