import '../style/style.css';
import 'react-circular-progressbar/dist/styles.css';

// react-bootstrap
import {Container, Carousel} from 'react-bootstrap';

//carousel
import art1 from '../img/art1.jpeg';
import art2 from '../img/art2.jpg';
import art3 from '../img/art3.jpg';
import art4 from '../img/art4.jpeg';
import art5 from '../img/art5.jpeg';
import art6 from '../img/art6.jpeg';
import art7 from '../img/art7.jpeg';
import art8 from '../img/art8.jpeg';
import hobby1 from '../img/hobby.jpeg'
import hobby2 from '../img/hobby1.jpg'
import hobby3 from '../img/hobby2.jpg'
import hobby4 from '../img/hobby3.jpg'

//animation on scroll
import AOS from 'aos';
import 'aos/dist/aos.css'; 
AOS.init();




const Skills = () => {
    
    return(
        
        <>
           
        <Container  fluid className="skills-div" id="Hobbies">
            <Container  fluid className="projects-header"><h1 className=''> My Hobbies </h1></Container>
            <Carousel data-aos="flip-up" className="skills-carousel" fade={true}  fluid> 

                <Carousel.Item  interval={1000}>
                    <img className="skills-carousel-img d-block=" src={art8}  alt="Forest Paint" title="Forest Paint" /><br/>
                </Carousel.Item>
                <Carousel.Item className="skills-carousel-item" interval={1000}>
                    <img  className="skills-carousel-img d-block"  alt="Ember the Dog"  title="Ember the Dog"  src={art1} /><br/>
                </Carousel.Item>

                <Carousel.Item interval={1000}>
                    <img className="skills-carousel-img d-block" alt="Ghibli Scenery"  title="Ghibli Scenery"src={art2} /><br/>
                </Carousel.Item>

                <Carousel.Item interval={1000} >
                    <img className="skills-carousel-img"  alt="Sunset Sceneries"  title="Sunset Sceneries"src={art3} /><br/>
                    
                </Carousel.Item>

                <Carousel.Item interval={1000}>
                    <img className="skills-carousel-img" alt="Abstract Dog and Waterfalls Painting" title="Abstract Dog and Waterfalls Painting"  src={art4} /><br/>
                </Carousel.Item>

                <Carousel.Item interval={1000}> 
                    <img className="skills-carousel-img" alt="Sunset Scenery" title="Sunset Scenery"  src={art5}/><br/>
                </Carousel.Item>

                <Carousel.Item interval={1000}>
                    <img className="skills-carousel-img" alt="Wheat Field and Sunset Watercolor"  title="Wheat Field and Sunset Watercolor"src={art6}/><br/>
                </Carousel.Item>

                <Carousel.Item interval={1000}>
                    <img className="skills-carousel-img" alt="Dog, Cat and Tree Watercolor Paintings" title="Dog, Cat and Tree Watercolor Paintings" src={art7}/><br/>
                </Carousel.Item>

                <Carousel.Item interval={1000}>
                    <img className="skills-carousel-img" alt="Mountain Climbing with Friends" title="Mountain Climbing with Friends" src={hobby1}/><br/>
                </Carousel.Item>

                <Carousel.Item interval={1000}>
                    <img className="skills-carousel-img" alt="Falls with Friends" title="Falls with Friends" src={hobby2}/><br/>
                </Carousel.Item>

                <Carousel.Item interval={1000}>
                    <img className="skills-carousel-img" alt="Mountain Scenery"  title="Mountain Scenery" src={hobby3}/><br/>
                </Carousel.Item>

                {/* <Carousel.Item interval={1000}>
                    <img className="skills-carousel-img" alt="Dog, Cat and Tree Watercolor Paintings" src={hobby4}/><br/>
                </Carousel.Item> */}

            
            
            </Carousel>

        </Container>
           
        </>
       
     
    )
}

export default Skills;