import '../style/style.css';
import '../style/granim.css';
import ReactAudioPlayer from 'react-audio-player';
// import AudioPlayer from 'react-h5-audio-player';
// import 'react-h5-audio-player/lib/styles.css';
import React, { useEffect } from "react";
import Granim from "granim";
import { BsFacebook, BsInstagram, BsLinkedin } from "react-icons/bs";


//react-animation
import {Animated} from "react-animated-css";

// react-bootstrap
import {Nav, Container, Navbar, Row, Col, Audio} from 'react-bootstrap';

//animation on scroll
import AOS from 'aos';
import 'aos/dist/aos.css'; 
AOS.init();

const Footer = () => {
    useEffect(() => {
        new Granim({
          element: '#canvas-image-blending-footer',
          direction: 'top-bottom',
          isPausedWhenNotInView: true,
          image : {
              source: 'https://cutewallpaper.org/21/hd-gradient/Color-Gradient-Wallpaper-76+-images-.jpg',
              blendingMode: 'multiply',
              stretchMode: ['stretch', 'stretch'],
          },
          states : {
            
              "default-state": {
                  gradients: [
                      ['#29323c', '#485563'],
                      ['#FF6B6B', '#556270'],
                      ['#80d3fe', '#7ea0c4'],
                      ['#f0ab51', '#eceba3']
                  ],
                  transitionSpeed: 2500
                  
              }
          }
      });
    
      }, []);


    return(
        
        <>
            <div className="content-footer">
                

                        
                <canvas className="footer-canvas" id="canvas-image-blending-footer"></canvas>


                    <Container fluid>
                        <Row className='footer-overlay' lg={12}>

                           
                                <Col className="footer-overlay-one" lg={8} sm={12}>
                                    <Container className='footer-container-one'>
                                        <Container className='footer-container-quote'>
                                            <p className='footer-brand'>Ljaneee.</p>
                                            <p className='footer-quote'>One important key to success is self-confidence. <br/>An important key to self-confidence is preparation.<br/></p> <p className='footer-quote-author'>- Arthur Ashe</p>
                                        </Container>
                                    
                                    </Container>
                                    
                                </Col>

                                <Col className="footer-overlay-two"  lg={4} sm={12}>
                                    <Container className='footer-container-two'>
                                        <Container className='footer-container-explore'>
                                             
                                                <p className='footer-overlay-two-title'> <br />Explore</p>
                                                <a href="#Home">Home</a> <br/>
                                                <a href="#AboutMe">About Me</a><br/>
                                                <a href="#Hobbies">Hobbies</a><br/>
                                                <a href="#Projects">Projects</a><br/>
                                            
                                        </Container>  

                                    

                                    </Container>
                                </Col>
                       
                      

                            {/* <Container fluid> */}
                                <Row  className='footer-contacts' lg={12}>
                                    <Row  sm={8} lg={4} >
                                      
                                        <Col  xs={12}> 
                                            <p>Phone Number</p>
                                            <a className='contact-number' href="tel:+63 977 206 9635">+63  977 206 9635</a>
                                        </Col>
                                        <Col xs={12} >
                                            <p>Email</p>
                                            <a className='contact-number' href="mailto:lhorena.deiparine@gmail.com">lhorena.deiparine@gmail.com</a>

                                        </Col>
                                        <Col  xs={12}>
                                            <p>Social Media</p>
                                            <div className="footer-social-buttons">
                                            <a href="https://www.facebook.com/ljane.deiparine.16" target="_blank" title="Facebook"><BsFacebook /></a>
                                                <a href="https://www.instagram.com/ljaneeed/" target="_blank" title="Instagram"><BsInstagram/></a>
                                                <a href="https://www.linkedin.com/in/lhorena-jane-deiparine-579907229" target="_blank" title="Linkedin"> <BsLinkedin/></a>
                                            </div>
                                        </Col>
                                    </Row>
                               
                                    
                                    
                                </Row>
                            {/* </Container> */}


                            
                        </Row>  

                     

                       
                    </Container>
                    <Container fluid>
                                <Row>
                            
                                    <Col className='footer-copyright'> 
                                        <Container container >  2022 Copyright: &nbsp;
                                        <a href="">LJLD</a> . All rights reserved.
                                        </Container>
                                    </Col>
                                </Row>
                            </Container>
                   

               

                          
                     
                    
                  
                        
                    
            </div>
        </>
       
     
    )
}

export default Footer;